/* AwardsSection.css */

.awards-timeline {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  margin-left: 2rem;
  padding: 0.5rem 0;
}

/* Timeline line and node */
.timeline-item {
  position: relative;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.timeline-line {
  position: absolute;
  left: -1.2rem;
  top: 1.2rem;
  width: 2px;
  height: calc(100% + 1.5rem);
  background: linear-gradient(to bottom, #90caf9 0%, #2196f3 100%);
  z-index: 0;
}

.timeline-node {
  position: absolute;
  left: -1.6rem;
  top: 0.6rem;
  width: 12px;
  height: 12px;
  background: #2196f3;
  border-radius: 50%;
  border: 3px solid #e3f2fd;
  box-shadow: 0 0 6px rgba(33, 150, 243, 0.4);
  z-index: 2;
}

/* Card */
.award-card {
  position: relative;
  background: linear-gradient(135deg, #f9fafb 0%, #f1f5f9 100%);
  border: 1px solid rgba(33, 150, 243, 0.15);
  border-radius: 10px;
  padding: 1rem 1.3rem;
  width: 100%;
  backdrop-filter: blur(6px);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.award-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(33, 150, 243, 0.12);
}

/* Header: left = title/organization, right = date */
.award-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1.5rem;
  flex-wrap: wrap;
}

@media (max-width: 600px) {
  .award-header {
    flex-direction: column;
    align-items: flex-start;
  }
  .award-header > div:last-child {
    text-align: left;
    align-items: flex-start;
    margin-top: 0.5rem;
  }
}
